#intro {
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    /* z-index: 3; */

}

.myPicture {
    position: absolute;
    top: 4rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}

.introContent {
    height: 100vh;
    width: 100vh;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
}

.hello {
    font-size: 1.7rem;
    font-weight: 200;
}

.introName {
    color: yellow;
}

.introP {
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}

.button {
    background: #fff;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;


}

.button-image {
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;

}

@media screen and (max-width: 840px) {
    .mypicture {
        right: -10vw;
    }

    .introContent {
        font-size: 10vw;
    }

    .hello {
        font-size: 4.5rem;
    }
}

@media screen and (max-width: 430px) {
    .mypicture {
        right: -20vw;
    }

    #intro {
        width: 100vh;
        max-width: 46rem;
    }
}